import React from "react";
import styles from "../css/main.module.css";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  BrowserRouter,
} from "react-router-dom";
import Lateinlex from "./Lateinlex";
import Texts from "./Texts";
import TikTok from "./TikTok";
import Home from "./Home";

function Main() {
  return (
    <div className={styles.container}>
      <header className={styles.header}>
        <ul>
          <li>
            <a href="/">Home</a>
          </li>
          <li>
            <a href="/lateinlex">Lateinlex</a>
          </li>

          <li>
            <a href="tiktok">TikToks</a>
          </li>
          <li>
            <a href="videos">Videos</a>
          </li>
        </ul>
      </header>

      <BrowserRouter basename="pseminar">
        <Switch>
          <Route path="/lateinlex">
            <Lateinlex></Lateinlex>
          </Route>
          <Route path="/texts">
            <Texts></Texts>
          </Route>
          <Route path="/tiktok">
            <TikTok></TikTok>
          </Route>
          <Route path="/">
            <Home></Home>
          </Route>
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default Main;
